import { AppBar, Box, Button, Card, CardActions, CardContent, Container, IconButton, Pagination, Stack, Toolbar, Typography } from "@mui/material";
import { Component, Fragment } from "react";
import { getPostThread } from "../../api/thread-service/ThreadService";
import Login from "../../component/login/Login";


class MainScreen extends Component{
    // {"googleId":"105171178520467521319","imageUrl":"https://lh3.googleusercontent.com/a/ACg8ocJs28YTJgqeyxusdTSaTkyrBUcOB2UIt8wYpTtlQIKc=s96-c","email":"bennyindra.official@gmail.com","name":"benny indra","givenName":"benny","familyName":"indra"}
    constructor(props){
        super(props)
        this.state = {
            username: undefined,
            token: '',
            email: '',
            isNeedLogin: true,
            threads: [],
            rows: [
                {title:'Frozen yoghurt', calories: 159, fat: 6.0, carbs: 24, protein: 4.0},
                {title:'Ice cream sandwich', calories: 237, fat: 9.0,carbs: 37, protein: 4.3},
              ],
            profileObj: undefined
        }

    }
    
    async componentDidMount(){
        try {
            const threads = await getPostThread();
            this.setState({
                threads: threads
            })
        } catch (error) {
            
        }
    }


    render(){
        return (
            <Fragment>
                <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static">
                    <Toolbar>
                        <IconButton size="large"
                            edge="start"
                            color="inherit"
                            aria-label="menu"
                            sx={{ mr: 2 }}
                            > 

                        </IconButton>
                        <IconButton />
                        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                            Forum Dayin
                        </Typography>
                        <Login label={this.state.username?this.state.username:'Login'} onSuccessLogin={(profileObj) => this.setState({username: profileObj.name, profileObj: profileObj})}/>
                    </Toolbar>
                </AppBar>
                    <Container maxWidth="xm">
                    <Typography variant="h3">Threads</Typography>
                    <hr/>
                    <Pagination count={10} variant="outlined" color="primary" size="large" />
                    <hr/>
                        <Stack spacing={2}>
    
                        {this.state.threads.map((row)=>(
                            <Card sm={{ minWidth: 275 }}>
                                <CardContent>                                           
                                    <Typography variant="h6" color="text.secondary" gutterBottom>{row.title}</Typography>
                                    <Typography variant="body">
                                        {row.body}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <Button size="small">Lihat</Button>
                                </CardActions>
                            </Card>
                        ))}
                        </Stack>
                        <hr/>
                        <Pagination count={10} variant="outlined" color="primary" size="large"  />
                        <hr/>
                    </Container>
                </Box>
            </Fragment>
            
        )
    }

}


export default MainScreen;